import React, { useState } from 'react';
import SharingButton from './components/SharingButton';
import medialist from './medialist';
import HtmlGenerator from './components/HtmlGenerator';
import CssGenerator from './components/CssGenerator';
import Generator from './components/Generator';
import "./css/checkboxes.css";
import "./css/sharingbuttons.css";
import "./css/main.css";

function Social() {
  //  Function used for selected social media. For start this is blank
  //  it will look like this: {facebook: true, twitter: false}
  //  at start it is empty, and for JS it is the same as if all values inside it are falsy.
  const [selected, setSelected] = useState({facebook: true});

  //  Custom URL for sharing. For start this is blank
  const [url, setUrl] = useState('www.okami.tech');

  //  Custom text for sharing. For start this is blank
  const [text, setText] = useState('Check this site!');

  // Size of the sharing buttons
  const [size, setSize] = useState('small');

  // Corner radius of the sharing buttons
  const [corners, setCorners] = useState('rounded');

  // Function that is called when user makes some changes to checkboxes
  const handleSelect = (event) => {
    const { name, checked } = event.target;

    // We create new 'selected' object, that includes previous 'selected' and the new one.
    // if 'name' is already present, JS will override it with the new value 'checked'.
    // this is clever, because it works both for selecting and deselecting!
    setSelected({
      ...selected,
      [name]: checked
    })
  };

  // Funstion that changes size
  const handleSize = (event) => {
    setSize(event.target.value)
  }

  // Funstion that changes corner radius
  const handleCorners = (event) => {
    setCorners(event.target.value)
  }

  return (
    <div className="Social">

      <Generator
        url={url}
        setUrl={setUrl}
        text={text}
        setText={setText}
        size={size}
        medialist={medialist}
        handleSelect={handleSelect}
        handleSize={handleSize}
        selected={selected}
        handleCorners={handleCorners}
        corners={corners}
      />

      <div className="social-buttons">
        {
          medialist.filter(option => {
            return(selected[option.name]);
          })
          .map(option => {
            return (
                <SharingButton key={option.name}
                  option={option}
                  text={text}
                  url={url}
                  size={size}
                  corners={corners}
                />
            )
          })
        }
      </div> 

      <div className="code-container">
        <HtmlGenerator
          medialist={medialist}
          selected={selected}
          text={text}
          url={url}
          size={size}
          corners={corners}
        />

        <CssGenerator
          medialist={medialist}
          selected={selected}
          text={text}
          url={url}
          size={size}
          corners={corners}
        />
      </div>

    </div>
  );
}

export default Social;
